
import { computed, defineComponent } from "vue";
export default defineComponent({
  name: "DownloadButton",
  props: {
    onClick: {
      type: Function,
      required: true,
    },
    filetype: {
      type: String,
      required: true,
    },
  },
  emits: ["update:filetype"],
  setup(props, { emit }) {
    const filetype = computed({
      get: () => props.filetype,
      set: (value) => emit("update:filetype", value),
    });

    return {
      filetype,
    };
  },
  data(): any {
    return {
      options: [
        { text: "png", value: "png" },
        { text: "jpg", value: "jpg" },
        { text: "bmp", value: "bmp" },
        { text: "ico", value: "ico" },
        { text: "gif", value: "gif" },
      ],
    };
  },
});
